



*{
  scroll-behavior: smooth;
 }
 .App {
  background-color: #39445a;
  min-height: 100vh;
  color: #fff;
  padding-top: 100px !important;
  padding-bottom: 70px;
  
}

@media (max-width: 700px){
  .App{
    padding-top: 70px;
  }
}


.pageTitle{
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: #fff;
  padding-top: 3em;
}

@media (max-width: 1000px){
  .pageTitle{
    
    font-size: 6.4vw;
    
  }
}
